@import '../variables/_variables.scss';


.Badge{
    width: 100%;    
    .row {
        width:100%;
        max-width: 650px;
    }
    &__row {
        width: 100%;
        margin-right:0px;
        margin-left:0px;
        .Badge-body {
            max-height: 400px;
            height: 100%;
            margin:60px 0px;
            padding:0px;            
            background: #ffffff;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
            border-radius: 8px 8px 8px 8px;
            overflow: hidden;
        
            &__background {
                display: flex;
                justify-content: center;
                padding: 0.5rem 0;
                height: 80px;
                background: #1b1b25;

                .Badge-body__img {
                    max-width: 120px;
                    width: 100%;
                }
        
            }
            &__email {
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 1rem 0;
                font-size: map-get($font-size--px, medium+2);
                .Badge-body__avatar {
                    max-width: 120px;
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    margin-right: 1rem;
                }
                .Badge-body__title {
                    font-weight: map-get($font-weight, bold);
                    font-size:map-get($font-size--title, max);
                }
        
            }
            .Badge-body__profesion {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 0.5rem 0;
                background: #f4f4f7;
                font-size: map-get($font-size--px, medium+2);
                .Badge-body__text {
                    font-weight: map-get($font-weight, regular);
                    font-size: map-get($font-size--text, medium);
                }
                .Badge-body__span {
                    font-weight: map-get($font-weight, regular);
                    font-size: map-get($font-size--text, min);
                }
        
            }
            .Badge-body__platziConf {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 54px;
                color: #98ca3f;
                font-weight: bold;
                font-style: italic;
                font-size: map-get($font-size--px, medium+1);
                .Badge-body__text {
                    font-weight: map-get($font-weight, regular);
                    font-size: map-get($font-size--text, min);
                }
            }
        }
    }
}